.Converter {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  height: 60%;

  transition: all .2s linear;
}

.Converter__inner {
  max-width: 48rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 15px;

  margin-bottom: 2rem;
}


@media (max-width: 768px) {
  .Converter {
    height: 100%;
    justify-content: center;
  }

  .Converter__inner {
    flex-direction: column;
  }
}

@media (max-width: 435px) {
  .Converter__title {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }
}

@media (max-width: 375px) {
  .Converter__title {
    margin-bottom: 3.5rem;
  }
}

@media (max-width: 320px) {
  .Converter__title {
    margin-bottom: 1.5rem;
  }
}